.slick-initialized .slick-slide {
  display: inline-grid;
  align-content: space-between;
}
.slick-initialized .slick-slide > div {
  width: inherit;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.slick-track {
  align-items: center;
  display: flex;
}
.slick-slide {
  float: none;
}
.slick-list {
  padding: 1px;
}

